.form-message {
  position: relative;
  padding-top: 5px;

  @media (min-width: $tablet-width) {
    padding-left: 160px;
  }
}

.form-message__label {
  display: block;
  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    margin-bottom: 18px;
    position: absolute;
    top: 21px;
    left: 0;
  }
}

.form-message__textarea {
  display: block;
  width: 100%;
  min-height: 142px;
  border: 2px solid $basic-black;
  padding: 15px;
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    font-size: 19px;
    line-height: 20px;
  }

  &:hover,
  &:focus,
  &:active {
    outline: none;
    border-color: $special-aquamarine;
  }
}
