.page-header {
  position: relative;

  @media (min-width: $tablet-width) {
    padding-left: 50px;
    padding-right: 50px;
    border-bottom: 1px solid rgba($basic-black, 0.1);
  }
}

.page-header__nav {
  margin: 0 auto;
}

.page-header__logo {
  padding: 0 30px;
  padding-top: 22px;
  padding-bottom: 16px;
  margin: 0 auto;

  &[href]:hover,
  &[href]:focus {
    opacity: 0.6;
  }

  &[href]:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    padding-left: 0;
    margin: 0;
    margin-right: auto;
  }

  @media (min-width: $desktop-width) {
    padding: 0 30px;
    padding-top: 9px;
    margin-left: 10px;
  }
}

.page-header__logo img {
  width: 86px;
  height: 35px;

  @media (min-width: $tablet-width) {
    width: 114px;
    height: 46px;
  }

  @media (min-width: $desktop-width) {
    width: 149px;
    height: 72px;
  }
}

.page-header__info {
  position: absolute;
  top: 110px;
  right: 0;
  margin: 0;
  font-size: 15px;
  font-weight: 400;
  z-index: 10;

  @media (max-width: $mobile-width-only) {
    display: none;
  }
}
